import React, { Component } from 'react'
import { Redirect } from 'react-router-dom';
// import { bindActionCreators } from 'redux'
import { connect } from 'react-redux'
import API from 'api'

class Form extends Component {
  state = {
    loading: false,
    title: '',
    description: '',
    body: '',
    tag: '',
  }

  submitHandle = (e) => {
    const { title, description, body, tag } = this.state
    const tagList = tag.length ? tag.split(',').map(v => v.trim()) : []
    
    this.setState(() => ({
      loading: true
    }))

    API.Articles.create({
      title,
      description,
      body,
      tagList
    }).then(data => {
      // console.log(data)

      this.setState(() => ({
        loading: false
      }))
    })

    e.preventDefault()
  }

  changeInput = name => e => {
    const { value } = e.target
    this.setState(() => ({
      [name]: value
    }))
  }

  render() {
    const { user, history } = this.props
    const { loading, title, description, body, tag } = this.state

    if(!user) {
      alert('로그인이 필요합니다')
    }

    return !user ?
      <Redirect to="/" />
      :
      (
        <>
          <h2>글 등록하기</h2>
          <form onSubmit={this.submitHandle}>
            <fieldset>
              <div>
                <label>
                  제목
                  <input type="text" disabled={loading} value={title} onChange={this.changeInput('title')} />
                </label>
              </div>
              <div>
                <label>
                  설명
                  <input type="text" disabled={loading} value={description} onChange={this.changeInput('description')} />
                </label>
              </div>
              <div>
                <label>
                  설명
                  <textarea cols="60" rows="10" disabled={loading} defaultValue={body} onChange={this.changeInput('body')}></textarea>
                </label>
              </div>
              <div>
                <label>
                  태그
                  <input type="text" disabled={loading} value={tag} onChange={this.changeInput('tag')} />
                </label>
              </div>
              { loading && <div>등록 중...</div> }
              <button type="submit" disabled={loading}>등록</button>{' '}
              <button type="button" disabled={loading} onClick={history.goBack}>취소</button>
            </fieldset>
          </form>
        </>
      )
  }
}

const mapStateToProps = (state) => ({
  user: state.auth.user,
})

const mapDispatchToProps = (dispatch) => ({
})

export default connect(mapStateToProps, mapDispatchToProps)(Form)