import React, { Component } from 'react'
import { Link } from 'react-router-dom';
import { connect } from 'react-redux'
import API from 'api'

class Home extends Component {
  state = {
    articlesCount: 0,
    articles: []
  }

  componentDidMount() {
    this.fetchArticles()
  }
  
  fetchArticles = async () => {
    const { data } = await API.Articles.all()
    const { articlesCount, articles } = data
    
    this.setState(() => ({
      articlesCount,
      articles
    }))
  }

  render() {
    const { articlesCount, articles } = this.state

    return (
      <>
        <h2>Home</h2>

        <p>total : {articlesCount}</p>

        <ul>
          {
            articles.map(v => (
              <li key={v.slug}>
                <p className="title"><Link to={`/article/${v.slug}`}>제목 : {v.title}</Link></p>
                <p className="desc">{v.description}</p>
                <div className="info">
                  <span className="author">작성자: {v.author.username}</span>{' '}
                  <span className="date">작성일: {v.createdAt}</span>{' '}
                  <span className="tag">Tag: {v.tagList.join(', ')}</span>
                </div>
              </li>
            ))
          }
        </ul>
      </>
    )
  }
}

const mapStateToProps = (state) => ({
})

const mapDispatchToProps = (dispatch) => ({
})

export default connect(mapStateToProps, mapDispatchToProps)(Home)