import React, { Component } from 'react'
import { connect } from 'react-redux'
import API from 'api'

class Article extends Component {
  state = {
    article: {
      author: {},
      tagList: []
    }
  }

  componentDidMount() {
    this.fetchArticles()
  }
  
  fetchArticles = async () => {
    const { data } = await API.Articles.get(this.props.match.params.slug)

    // console.log(data.article)
    
    this.setState(() => ({
      article: data.article
    }))
  }

  render() {
    const { article } = this.state

    return (
      <>
        <h2>{article.title}</h2>
        <p className="desc">{article.description}</p>
        <div className="info">
          <span className="author">작성자: {article.author.username}</span>{' '}
          <span className="date">작성일: {article.createdAt}</span>{' '}
          <span className="tag">Tag: {article.tagList.join(', ')}</span>
        </div>
        <div className="body">{article.body}</div>
      </>
    )
  }
}

const mapStateToProps = (state) => ({
})

const mapDispatchToProps = (dispatch) => ({
})

export default connect(mapStateToProps, mapDispatchToProps)(Article)